.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
body{
  background-color: #121c39;
  display: flex;
  align-items: center;
  justify-content: center;
}
.cont{
  background-color: #014dad;
  height: 85vh;
  width: 500px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  border-radius: 9px;
}
.neon-text{
  color:#00bfff;
  font-weight: 700;
  margin-bottom: 20px;
}
a{
  color: black;
  text-decoration: none;
  background-color: aliceblue;
  padding: 20px 30px;
  border-radius: 50px 0;
  margin-top: 25px;
  font-size: 16px;
  font-weight: 700;
  box-shadow: 3px 3px 3px 3px #141414 ;


}
a:hover{
  box-shadow: none;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
